.navbar-section{
    position: sticky;
    top: 0;
    z-index: 10;
}

.nav{
    font-family: 'Libre Baskerville', serif;
    padding: 20px 0;
    display: flex;
    justify-content: space-evenly;
    background-color: #2d333b;
    color: rgb(255, 255, 255);
}

.hamburger{
    position: absolute;
    top: 17px;
    right: 20px;

    font-size: 30px;
    display: none;
    cursor: pointer;
}

.bar{
    height: 3px;
    width: 100%;
    border-radius: 5px;
    background-color: rgb(255, 255, 255);
}

.logo-nav{
    color: rgb(255, 154, 68);
    font-size: 20px;
    cursor: pointer;
}

.logo-nav-span{
    color: rgb(255, 255, 255);
}

.nav-links{
    display: flex;
}

li{
    padding: 0 10px;
    list-style: none;
}

a{
    text-decoration: none;
    color: rgb(255, 255, 255);
    font-size: 17px;
}

.button-components{
    background-color: rgb(255, 154, 68);
    color: rgb(0, 0, 0);
    padding: 10px;
    border-radius: 5px;
}

.hamburger-links{
    padding-bottom: 10px;
    position: absolute;
    font-family: 'Libre Baskerville', serif;
    background-color: #2d333b;
    width: 200px;
    right: 0;
    text-align: center;
    display: none;
    z-index: 10;
}


@media (max-width: 750px) {
    .nav{
        padding: 20px;
        justify-content: space-between;
        flex-direction: column;
        align-items: flex-start;
    }

    .nav-links{
        display: none;
    }

    .hamburger{
        display: flex;
    }

    li{
        padding: 10px;
    }

    .hamburger-links{
        display: block;
    }
}