.standard-picture{
    font-family: 'Libre Baskerville', serif;
    display: grid;
    place-items: center;
    padding: 50px 0;
}

.overlay-standard-picture{
    padding: 0 50px;
    display: flex;
    gap: 100px;
}

.image-standard-picture{
    background-image: url("../images/StandardPicture.jpg");
    background-size: cover;
    width: 400px;
    height: 250px;
}

.title-standard-picture{
    font-size: 20px;
}

.info-standard-picture{
    padding-top: 20px;
    font-size: 15px;
    max-width: 500px;
}

.main-info-standard-picture{
    font-size: 17px;
    padding-top: 20px;
    color: rgb(65, 65, 65);
}


@media (max-width: 999px) {
    .overlay-standard-picture{
        display: grid;
        place-items: center;
        gap: 50px
    }

    .title-standard-picture{
        text-align: center;
    }
}

@media (max-width: 499px) {
    .image-standard-picture{
        width: 100%;
    }

    .overlay-standard-picture{
        padding: 0 20px;
    }
}